<script>
export default {
  name: "OfferTable",
  props: {
    offers: {
      type: Array,
    },
  },
  data() {
    return {
      offerToCopy: [],
      downloadType: "",
    };
  },
  methods: {
    onScroll(el) {
      let height = el.getBoundingClientRect().height;
      let scrollHeight = el.scrollHeight;
      let top = el.scrollTop;
      this.$emit("scroll", top);
      Math.ceil(top + height) == scrollHeight.toFixed(0) &&
        this.$emit("scrolled");
    },
  },
  mounted() {
    const el = this.$refs.table;
    el &&
      el.addEventListener("scroll", () => {
        this.onScroll(el);
      });
  },
  watch: {
    offerToCopy() {
      this.$emit("copy", this.offerToCopy);
    },
  },
  emits: ["download", "scroll", "scrolled", "copy"],
};
</script>

