<template>
  <section class="offer">
    <h1 class="offer__title page__title">Коммерческое предложение</h1>
    <div class="offer-header page-wrapper" :class="{ shadow: scrollStart }">
      <div class="offer-header__buttons">
        <div class="offer_create">
          <base-button
            :type="'filled'"
            @clicked="$router.push({ name: 'offerCreate' })"
          >
            Создать <span class="upp" style="margin-left: 2px"> кп</span>
          </base-button>
        </div>
        <div class="offer_copy">
          <base-button
            :type="'outlined'"
            @clicked="copy"
            :disabled="!offerToCopy.length"
          >
            Копировать
          </base-button>
        </div>
      </div>
      <div class="offer-search">
        <base-input
          :id="'offerSearch'"
          :placeholder="
            !$isMobile
              ? 'Поиск по номеру КП, конечному заказчику, региону, контрагенту'
              : 'Поиск'
          "
          :search="true"
          :searchIcon="true"
          :suggestionWidth="380"
          :suggestions="suggestions"
          @setSuggestion="getOffers"
          @keypressEnter="getOffers"
          @clear="(query = ''), getOffers()"
          @input="getOffers('search')"
          v-model="query"
        />
      </div>
      <div class="offer-filters__button" v-if="$isMobile">
        <div
          class="offer-filters__count"
          v-if="counterparty || region || customer || manager"
        >
          3
        </div>
        <base-button :type="'outlined'" @click="open = true">
          <img src="@/assets/icons/filters.svg" alt="Filters" />
        </base-button>
      </div>
    </div>
    <div
      class="offer-filters page-wrapper"
      :class="{ open: showContent }"
      v-if="!$isMobile || ($isMobile && open)"
    >
      <div class="offer-filters__close" v-if="$isMobile">
        <img
          src="@/assets/icons/clear.svg"
          alt="Close"
          @click="closeDropdown"
        />
      </div>
      <h4 class="offer-filters__title page__title" v-if="$isMobile">Фильтры</h4>
      <div class="offer-filters_counterparty">
        <base-input
          :id="'offerSearch'"
          :placeholder="'Контрагент'"
          :search="true"
          :suggestions="conterparties"
          @input="getOffers('search')"
          @keypressEnter="getOffers"
          @setSuggestion="getOffers(s)"
          v-model="counterparty"
        />
      </div>
      <div class="offer-filters_region">
        <base-select
          :options="regions"
          :placeholder="'Регион'"
          v-model="region"
          @change="getOffers"
        />
      </div>
      <div class="offer-filters_customer">
        <base-select
          :options="customers"
          :placeholder="'Конечный заказчик'"
          v-model="customer"
          @change="getOffers"
        />
      </div>
      <div class="offer-filters_manager">
        <base-select
          :options="managers"
          :placeholder="'Менеджер'"
          v-model="manager"
          @change="getOffers"
        />
      </div>
      <div class="offer-filters__apply page-buttons" v-if="$isMobile">
        <div class="offer-filters__apply-button">
          <base-button :type="'filled'" @click="closeDropdown">
            Применить
          </base-button>
        </div>
      </div>
    </div>
    <div class="offer-content page-wrapper">
      <offers-table
        :offers="offers.slice(0, count)"
        @copy="
          (value) => {
            offerToCopy = value;
          }
        "
        @scroll="
          (top) => {
            top > 0 ? (scrollStart = true) : (scrollStart = false);
          }
        "
        @scrolled="
          () => {
            count += 5;
          }
        "
      />
    </div>
  </section>
</template>

<script>
import BaseButton from "@/components/common/Base.Button.vue";
import BaseInput from "@/components/common/Base.Input.vue";
import BaseSelect from "@/components/common/Base.Select.vue";
import mobileDropdown from "@/mixins/mobileDropdown";
import OffersTable from "./components/OffersTable.vue";

export default {
  components: {
    BaseButton,
    BaseInput,
    BaseSelect,
    OffersTable,
  },
  data() {
    return {
      query: "",
      counterparty: "",
      region: "",
      customer: "",
      manager: "",
      count: 10,
      scrollStart: false,
      offerToCopy: [],
      regions: [
        {
          key: "Не Выбран",
          value: "",
        },
      ],
      offers: [],
      conterparties: [],
      suggestions: [],
      customers: [
        {
          key: "Не Выбран",
          value: "",
        },
      ],
      managers: [],
    };
  },
  mounted() {
    this.getOffers();
    this.getRegions();
  },
  methods: {
    copy() {
      const offer = this.offers.find((i) => i.id == this.offerToCopy[0]);
      const products = [];
      if (offer.offer_products.length) {
        for (let p of offer.offer_products) {
          products.push({
            product: p.id,
            price: p.price,
            discount: p.discount,
            count: p.count,
            cost: p.cost,
          });
        }
      }
      console.log(products);
      const data = {
        counterparty: (offer.counterparty && offer.counterparty.id) || null,
        end_customer: (offer.end_customer && offer.end_customer.id) || null,
        company: (offer.company && offer.company.id) || null,
        offer_products: products || null,
        tech_docs_format: offer.tech_docs_format,
      };
      this.$api.post(`${process.env.VUE_APP_API}/offer`, data).then((res) => {
        this.$router.push({ name: "offerCreate", query: { id: res.data.id } });
      });
    },
    getOffers(type = null) {
      let params = {
        [this.query && "search"]: this.query,
        [this.counterparty && "counterparty"]: this.counterparty,
        [this.customer && "end_customer"]: this.customer,
        [this.region && "end_customer__region"]: this.region,
      };
      // clear all filters fields
      this.conterparties = [];
      this.suggestions = [];
      this.customers = [
        {
          key: "Не Выбран",
          value: "",
        },
      ];
      // general request
      this.$api
        .get(`${process.env.VUE_APP_API}/offer`, { params })
        .then((res) => {
          console.log(res.data.results);
          if (type !== "search") this.offers = [];
          // loop for get all fields values
          for (const o of res.data.results) {
            if (type !== "search") this.offers.push(o);
            if (type == "search") {
              // get search suggestions
              if (this.query) {
                this.suggestions.push({
                  key: o.id,
                  value: o.id,
                });
              }
            }
            // get customers
            const customer = {
              key: o.end_customer ? o.end_customer.name : "",
              value: o.end_customer ? o.end_customer.id : "",
            };
            if (
              !this.customers.some((i) => i.value === customer.value) &&
              customer.key
            )
              this.customers.push(customer);

            // get counterparties
            const counterparty = {
              key: o.counterparty.name,
              value: o.counterparty.id,
            };
            if (
              !this.conterparties.some((c) => c.value === counterparty.value) &&
              counterparty.key &&
              this.counterparty
            )
              this.conterparties.push(counterparty);
          }
        })
        .catch((err) => console.log(err));
    },
    getRegions() {
      this.$api.get(`${process.env.VUE_APP_API}/regions`).then((res) => {
        for (let r of res.data) {
          const opt = {
            key: r.region,
            value: r.region,
          };
          !this.regions.some((i) => i.key == opt.key) && this.regions.push(opt);
        }
      });
    },
  },
  mixins: [mobileDropdown],
};
</script>

